body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.card-container {
  box-shadow: 0px 1px 5px 2px #cecece;
  overflow: auto;
  margin-bottom: 3rem;
  margin-top: 3rem;
  padding-bottom: 1rem;
}
.header {
  font-size: 1.5rem;
  font-family: sans-serif;
  margin-bottom: 1rem;
  font-weight: bold;
  margin: 1rem;
}
.card-item {
  width: 340px;
  height: 240px;
  text-align: left;
  margin: 14px;
  float: left;
  border-radius: 4px;
  border-top: 2px solid #1175ca;
  box-shadow: 0 2px 2px 1px #cecece;
}
.problem {
  height: 80px;
  font-size: 1.5rem;
  padding-left: 1rem;
  padding-top: .5rem;
  padding-right: .5rem;
}
.desc {
  height: 110px;
  font-size: 1rem;
  padding: 1rem;
  word-break: break-word;
}
.read-more {
  width: 100%;
  height: 50px;
  background: #f6e4ce47;
  padding: 10px 20px;
  font-weight: 500;
  text-decoration: underline;
  border-top: 1px solid #f8e8d5;
  cursor: pointer;
}